  }
  .tariffCards:after {
    position: absolute;
    bottom: -27px;
    left: 5%;
    content: '';
    width: 65%;
    height: 10px;
    border-radius: 100%;
    background-image: -webkit-radial-gradient(rgba(34,50,84,0.04), rgba(34,50,84,0));
    background-image: radial-gradient(rgba(34,50,84,0.04), rgba(34,50,84,0));
  }
  .tariffCards > div {
    position: relative;
    width: 300px;
    height: 170px;
    border-radius: 12px;
    color: #fff;
    -webkit-transform: rotateX(45deg) rotateY(-15deg) rotate(45deg);
            transform: rotateX(45deg) rotateY(-15deg) rotate(45deg);
    -webkit-transition: all 0.4s ease;
    transition: all 0.4s ease;
    overflow: hidden;
    cursor: pointer;
  }
  .tariffCards > div:after {
    position: absolute;
    top: -70px;
    left: 0;
    content: '';
    width: 200%;
    height: 200%;
    background-image: -webkit-linear-gradient(30deg, rgba(255,255,255,0) 20%, rgba(255,255,255,0.1), rgba(255,255,255,0) 80%);
    background-image: linear-gradient(60deg, rgba(255,255,255,0) 20%, rgba(255,255,255,0.1), rgba(255,255,255,0) 80%);
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }
  .tariffCards > div img {
    margin-top: 15px;
    pointer-events: none;
  }
  .tariffCards > div h3 {
    position: absolute;
    bottom: 30px;
    left: 15px;
    font-size: 30px;
    font-weight: 500;

  }
  .tariffCards > div span {
    position: absolute;
    bottom: 15px;
    left: 15px;
    font-size: 17px;
