    padding:0px 60px !important;
}

#blogs img {
    width: 100%;
    vertical-align: middle;
  
}

#blogs .blog-item img {
  display: block;
}

#blogs .blog-item a {
    display: flex;
    padding: 50px;
    overflow: hidden;
    align-items: center;
    max-width: 1000px;
    margin: auto;
    color: #333;
    font-family: 'Lato', sans-serif;
    font-size: 16px;
    border-radius: 6px;
    margin-bottom: 50px;
    text-decoration: none;
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
}

#blogs .blog-item a:hover {
    box-shadow: 0 10px 50px 0 rgba(0, 0, 0, 0.2);
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
}

#blogs .blog-item .icon {
    width: 35%;
    object-fit: cover;
    margin-right: 50px;
    border-radius: 6px;
    overflow: hidden;
    /* box-shadow: 0 15px 26px rgba(0, 0, 0, 0.4); */
}

#blogs .blog-item .content {
    width: 65%;
}

#blogs .blog-item .content .title {
    font-size: 21px;
    text-transform: capitalize;
    color: #25aae1;
    font-weight: 600;
    font-family: 'Lato', sans-serif;
}



#blogs .blog-item .content .title .blog-date {
    font-size: 12px;
    margin-left: 20px;
    color: #333;
    font-family: 'Lato', sans-serif;
}

#blogs .blog-item .content .title .blog-date:before {
    content: '/';
    color: #2bb673;
    font-size: 25px;
    top: auto;
    position: relative;
    margin-right: 10px;
}

#blogs .blog-item .content .rounded {
    margin-left: 0;
    margin-top: 10px;
    margin-bottom: 10px;
}

#blogs .blog-item .item-arrow {
    height: 20px;
    width: 15%;
    position: relative;
    text-align: center;
    font-size: 35px;
    left: 0;
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
}

#blogs .blog-item a:hover > .item-arrow {
    /*padding-left: 40px;*/
    color: #25aae1;
    left: 30px;
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
}

#blogs .rounded {
    width: 70px;
    height: 5px;
    border-radius: 6px;
    background-image: -webkit-gradient(linear, left top, right top, from(#25aae1), to(#40e495));
    background-image: -webkit-linear-gradient(left, #25aae1, #40e495);
    background-image: linear-gradient(to right, #25aae1, #40e495);
    /* background: #2bb769; */
    margin-left: 0;
    margin-top: 10px;
    margin-bottom: 10px;
}

#blogs .slick-list{
    padding: 20px 0px ;
}
@media screen and (max-width: 768px){

    #blogs .blog-item .content {
        width: 100%;
        margin-top: 35px;
    }

    #blogs .blog-item .content .title {
        font-size: 18px;
    }

    #blogs .blog-item .item-arrow {
        margin: auto;
    }

    #blogs .blog-item .icon {
        width: 100%;
    }

    #blogs .blog-item .icon img {
        width: 100%;
        height: 150px;
        object-fit: cover;
    }

    #blogs .blog-item {
        width: 92%;
        margin: auto;
    }

    #blogs .blog-item a {
        display: block;
        padding: 30px;
    }

}

